import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Icon from './Icon';
import svg from 'img/right.svg';
import cx from 'classnames';
import st from './Icon.module.sass';

class RightIcon extends Component {
  render () {
    const p = {...this.props, className: cx(st.px14, this.props.className)};
    return (<div className={cx(st.px24, st.right, st.right5px)}><Icon icon={svg} {...p} /></div>);
  }
}

RightIcon.propTypes = {
  children: PropTypes.any,
  className: PropTypes.string,
  onClick: PropTypes.func,
};

export default RightIcon;


