import React, {Component} from 'react';
import PropTypes from 'prop-types';
import st from './UiKit.module.sass';
import cx from 'classnames';

class Scroll extends Component {
  render() {
    const {children} = this.props;

    return (
      <div className={cx(st.scrollBox, 'otc-date-close-if-scroll-this')}>
        <div className={st.scrollContent}>
          <div className={st.scrollContentBox}>
            {children}
          </div>
        </div>
      </div>
    );
  }
}

Scroll.propTypes = {
  children: PropTypes.element
};

export default Scroll;


