import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {timeMask} from 'utils/date';
import TimeIcon from '../icons/TimeIcon';
import MaskedInput from './MaskedInput/MaskedInput';
import st from './Time.module.sass';

class Time extends Component {
  state = {
    value: ''
  };

  componentWillMount() {
    const {defaultValue} = this.props;
    this.setState({value: defaultValue});
  }

  handlerOnInput = (e) => {
    const {onChange} = this.props;
    const value = timeMask(e.target.value);
    this.setState({value});
    if (onChange) onChange(value);
  };



  render() {
    const {value: sttValue} = this.state;
    const {
      onSave,
      className: cnBox,
      value = sttValue,
      placeholder,
      disableSave,
      error,
      onClickIcon,
    } = this.props;
    return (
      <div className={cx(st.tmBox, cnBox)}>
        <div className={cx(st.tmInputBox)}>
          <MaskedInput
            className={cx(st.tmInput, {[st.inputError]: error})}
            placeholderChar={' '}
            mask={[/[0-9]/, /[0-9]?/, ':', /[0-9]/, /[0-9]?/]}
            guide={false}
            keepCharPositions={true}
            placeholder={placeholder}
            value={value}
            onChange={this.handlerOnInput}
          />
          <TimeIcon
            className={cx(st.tmInputIconBox, st.tmInputIcon, {[st.btmIcon]: !!onClickIcon})}
            onClick={onClickIcon}
          />
        </div>
        {onSave && <div className={cx(st.tmButton, {[st.tmButtonDisable]: disableSave})} onClick={disableSave || onSave}>Применить</div>}
      </div>
    );
  }
}

Time.defaultProps = {
  placeholder: '00:00',
};

Time.propTypes = {
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  onSave: PropTypes.func,
  onChange: PropTypes.func,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  disableSave: PropTypes.bool,
  error: PropTypes.bool,
  onClickIcon: PropTypes.func,
};

export default Time;


