import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {constants} from 'utils/date';
import Years from 'components/common/Years';
import NextBack from './NextBack';
import st from './Months.module.sass';
import tm from 'components/styles/Team.module.sass';

class Months extends Component {
  static showType = {
    months: 'MONTHS',
    years: 'YEARS'
  };

  state = {
    year: -1,
    showType: Months.showType.months,
  };

  handlerOnClickYear = () => {
    const { onClickYear } = this.props;
    if (onClickYear) onClickYear();
    else this.setState({showType: Months.showType.years});
  };


  render() {
    const {
      months, month, onChange, className: cnBox, year: prpYear} = this.props;
    let {year, showType} = this.state;
    if (year === -1) year = prpYear;

    const renderingMonths = (
      months.map((a, i) => (
        <div
          key={a}
          className={cx(st.monthsCell, tm.teamCalendarItem, {[st.monthSelected]: prpYear === year && month === i + 1})}
          onClick={() => onChange && onChange(year, i + 1)}
        >
          {a}
        </div>
      ))
    );

    const renderRow = (items, key) => {
      return (
        <div key={'months-row-' + key} className={cx(st.monthsRow)}>
          {items}
        </div>
      );
    };

    if (showType === Months.showType.years) {
      return (
        <Years
          className={cx(st.monthsBox, cnBox)}
          year={year}
          onChange={(y) => this.setState({year: y, showType: Months.showType.months})}
        />
      );
    }

    return (
      <div className={cx(st.monthsBox, cnBox)}>
        <NextBack
          onNext={() => this.setState({year: year + 1})}
          onBack={() => this.setState({year: year - 1})}
        >
          <div className={st.yearBox} onClick={this.handlerOnClickYear}>{year}</div>
        </NextBack>
        <div className={st.monthsTable}>
          {renderRow([renderingMonths[0], renderingMonths[1], renderingMonths[2]], 0)}
          {renderRow([renderingMonths[3], renderingMonths[4], renderingMonths[5]], 1)}
          {renderRow([renderingMonths[6], renderingMonths[7], renderingMonths[8]], 2)}
          {renderRow([renderingMonths[9], renderingMonths[10], renderingMonths[11]], 3)}
        </div>
      </div>
    );
  }
}

Months.defaultProps = {
  lg: constants.lg.ru
};

Months.propTypes = {
  year: PropTypes.number,
  month: PropTypes.number,
  months: PropTypes.arrayOf(PropTypes.string),
  onChange: PropTypes.func,
  className: PropTypes.string,
  onClickYear: PropTypes.func,
};

export default Months;


