import React from 'react';
import cx from 'classnames';
import st from './NextBack.module.sass';
import LeftIcon from '../icons/LeftIcon';
import RightIcon from '../icons/RightIcon';
import tm from 'components/styles/Team.module.sass';

function NextBack(props) {
  const {children, onNext, onBack, className: cnBox} = props;
  return (
    <div className={cx(st.nextbackBox, st.nextbackHeaderStyle, cnBox, st.teamCalendarTitle)}>
      <div onClick={onBack}><LeftIcon className={st.nextbackGoLeftRightIcon} /></div>
      <div>
        <div className={cx(st.nextbackHeader, tm.teamCalendarTitleLocation)}>{children}</div>
      </div>
      <RightIcon onClick={onNext} className={st.nextbackGoLeftRightIcon} />
    </div>
  );
}

export default NextBack;


