import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import CalendarRange from 'components/common/CalendarRange';
import DateRangeField from 'components/common/DateRangeField';
import Days from 'components/common/Days';
import {constants, datetimeRangeToObjectValue, datetimeFromObject, datetimeCurrentToObject} from 'utils/date';
import tm from 'components/styles/Team.module.sass';
import st from './OtcDateRange.module.sass';

class OtcDateRange extends Component {
  state = {
    open: false,
    valueFrom: '',
    valueTo: '',
    changeType: ''
  };

  handlerInputOnChange = (value) => {
    const {from, to} = datetimeRangeToObjectValue(value);

    this.setState({valueFrom: from, valueTo: to, open: false});
    if (from && to) {
      const {onChange} = this.props;
      if (onChange) onChange(from, to);
    }
  };

  componentWillMount() {
    const {defaultValueFrom, defaultValueTo} = this.props;
    this.setState({valueFrom: defaultValueFrom, valueTo: defaultValueTo});
  }

  handlerOnClickCurrentDay = () => {
    const {showTime} = this.props;
    const {year, month, day} = datetimeCurrentToObject();

    let timeTo = showTime ? {hours: 23, minutes: 59} : {};
    let timeFrom = showTime ? {hours: 0, minutes: 0} : {};

    const to = datetimeFromObject({year, month, day, time: timeTo});
    const from = datetimeFromObject({year, month, day, time: timeFrom});

    this.setState({open: false, valueFrom: from, valueTo: to});
  };

  handlerOnClickCurrentWeek = () => {
    const {year, month, day} = datetimeCurrentToObject();

    const to = datetimeFromObject({year, month, day});
    const from = datetimeFromObject({year, month, day: day - 7});

    this.setState({open: false, valueFrom: from, valueTo: to});
  };

  handlerOnClickCurrentMount = () => {
    const {year, month, day} = datetimeCurrentToObject();

    const to = datetimeFromObject({year, month, day});
    const from = datetimeFromObject({year, month: month - 1, day});

    this.setState({open: false, valueFrom: from, valueTo: to});
  };

  render() {
    const {
      placeholderFrom, placeholderTo,
      showTime, showFastRange,
      gteCurrentDate,
      lteCurrentDate,
    } = this.props;
    const {valueFrom, valueTo, open, changeType} = this.state;

    const value = `${valueFrom } - ${valueTo}`;

    const renderCalendar = (left) => (
      <CalendarRange
        className={cx(st.rootCalendar)}
        classBox={cx({[st.rcLeft]: left})}
        gteCurrentDateFrom={gteCurrentDate}
        lteCurrentDateFrom={lteCurrentDate}
        lteCurrentDateTo={lteCurrentDate}
        gteCurrentDateTo={gteCurrentDate}
        onChange={this.handlerInputOnChange}
        value={value}
        showTime={showTime}
        type={Days.type.range}
        changeType={changeType}
        bottomCmp={showFastRange && (
          <div className={st.bottomCmp}>
            <div className={cx(st.bottomButton, tm.teamFastRange)} onClick={this.handlerOnClickCurrentDay}>за день</div>
            {!lteCurrentDate && <div className={cx(st.bottomButton, tm.teamFastRange)} onClick={this.handlerOnClickCurrentWeek}>за неделю</div>}
            {!lteCurrentDate && <div className={cx(st.bottomButton, tm.teamFastRange)} onClick={this.handlerOnClickCurrentMount}>за месяц</div>}
          </div>
        )}
      />
    );

    return (
      <DateRangeField
        onChange={(f, t) => this.handlerInputOnChange(`${f} - ${t}`)}
        onInput={() => {this.setState({open: false});}}
        placeholderFrom={placeholderFrom}
        placeholderTo={placeholderTo}
        valueFrom={valueFrom}
        valueTo={valueTo}
        onFocusTo={() => this.setState({open: constants.rangeValueType.to !== changeType || true, changeType: constants.rangeValueType.to})}
        onFocusFrom={() => this.setState({open: constants.rangeValueType.from !== changeType || true, changeType: constants.rangeValueType.from})}
        onClickTo={() => this.setState({open: constants.rangeValueType.to !== changeType || !open, changeType: constants.rangeValueType.to})}
        onClickFrom={() => this.setState({open: constants.rangeValueType.from !== changeType || !open, changeType: constants.rangeValueType.from})}
        popUpFrom={open && constants.rangeValueType.from === changeType && renderCalendar()}
        popUpTo={open && constants.rangeValueType.to === changeType && renderCalendar(true)}
        ignoreTime={!showTime}
      />
    );
  }
}

OtcDateRange.defaultProps = {
  showTime: true,
  showFastRange: true,
  lg: constants.lg.ru,
  gteCurrentDate: false,
  lteCurrentDate: false,
  placeholderFrom: 'С',
  placeholderTo: 'По'
};

OtcDateRange.propTypes = {
  placeholderFrom: PropTypes.string,
  defaultValueFrom: PropTypes.string,
  placeholderTo: PropTypes.string,
  defaultValueTo: PropTypes.string,
  onChange: PropTypes.func,
  showTime: PropTypes.bool,
  showFastRange: PropTypes.bool,
  lg: PropTypes.string,
  gteCurrentDate: PropTypes.bool,
  lteCurrentDate: PropTypes.bool,
};

OtcDateRange.descriptionProps = {
  placeholderFrom: 'подсказка, для значения поля `с`, отображаемая в поле для ввода, если оно пустое',
  defaultValueFrom: 'значение поля `с` по умолчанию, отображается если пользователь не менял значение поля',
  placeholderTo: 'подсказка, для значения поля `по`, отображаемая в поле для ввода, если оно пустое',
  defaultValueTo: 'значение поля `по` по умолчанию, отображается если пользователь не менял значение поля',
  onChange: 'функция вызываемая при изменении значения поля',
  showTime: 'отображать поле для ввода даты',
  showFastRange: 'отображать кнопки для быстрого ввода диапазона',
  gteCurrentDate: 'не больше текущей даты',
  lteCurrentDate: 'не меньше текущей даты',
};


export default OtcDateRange;


