import React, {Component} from 'react';
import OtcDate from 'components/OtcDate/OtcDate';
import OtcDateRange from 'components/OtcDateRange/OtcDateRange';
import OtcDateYear from 'components/OtcDateYear/OtcDateYear';
import OtcDateMonth from 'components/OtcDateMonth/OtcDateMonth';
import st from './UiKit.module.sass';
import Scroll from './Scroll';
import Box from './Box';

class UiKit extends Component {
  state = {
    otcDate: {},
    otcDateTime: {},
    otcDateRange: {},
    otcDateTimeRange: {},
    otcDateMonth: {},
    otcDateYear: {}
  };

  render() {
    const {otcDateTime, otcDate, otcDateRange, otcDateMonth, otcDateYear, otcDateTimeRange} = this.state;

    return (
      <div className={st.uikit}>
        <Scroll>
          <div style={{display: 'flex'}}>

            <OtcDate
              style={{width: '100%'}}
              onChange={(v) => this.setState({...otcDate, value1: v})}
              defaultValue={'2018-01-01'}
              placeholder={otcDate.placeholder}
            />

            <OtcDate
              style={{width: '100%'}}
              onChange={(v) => this.setState({...otcDate, value1: v})}
              defaultValue={'2018-01-01'}
              placeholder={otcDate.placeholder}
            />

          </div>
        </Scroll>

        <Box
          label='Выбрать одну дату без времени (DD.MM.YYYY), т.е. с точностью до дня'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate
            onChange={(v) => this.setState({...otcDate, value1: v})}
            defaultValue={'2018-01-01'}
            placeholder={otcDate.placeholder}
          />
        </Box>
        <Box
          label='Выбрать одну дату со временем (DD.MM.YYYY hh24:mi), т.е. с точностью до минуты.'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate showTime={true} onChange={(v) => this.setState({...otcDateTime, v})} defaultValue={otcDateTime.value} placeholder={otcDateTime.placeholder}/>
        </Box>
        <Box
          label='Выбрать одну дату со временем (DD.MM.YYYY hh24:mi), т.е. с точностью до минуты. (время обязательно для заполнения)'
          params={OtcDate}
          name={'OtcDate'}
        >
          <OtcDate showTime={true} timeIsRequired={true} onChange={(v) => this.setState({...otcDateTime, v})} defaultValue={otcDateTime.value} placeholder={otcDateTime.placeholder}/>
        </Box>
        <Box
          label='Выбрать диапазон дат: С и ПО'
          params={OtcDateRange}
          name={'OtcDateRange'}
        >
          <OtcDateRange showTime={false} onChange={(v) => this.setState({...otcDateRange, v})} {...otcDateRange}/>
        </Box>
        <Box
          label='Выбрать диапазон дат со временем: С и ПО'
          params={OtcDateRange}
          name={'OtcDateRange'}
        >
          <OtcDateRange showTime={true} onChange={(v) => this.setState({...otcDateTimeRange, v})} {...otcDateTimeRange}/>
        </Box>
        <Box
          label='Выбрать месяц (для Плана Закупок). (MM.YYYY или MMM YYYY)'
          params={OtcDateMonth}
          name={'OtcDateMonth'}
        >
          <OtcDateMonth onChange={(v) => this.setState({...otcDateMonth, v})} {...otcDateMonth}/>
        </Box>
        <Box
          label='Выбрать год (YYYY)'
          params={OtcDateYear}
          name={'OtcDateYear'}
        >
          <OtcDateYear onChange={(v) => this.setState({...otcDateYear, v})} {...otcDateYear}/>
        </Box>
      </div>
    );
  }
}

export default UiKit;


