import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import Icon from './Icon';
import svg from 'img/left.svg';
import st from './Icon.module.sass';

class LeftIcon extends Component {
  render () {
    const p = {...this.props, className: cx(st.px14, this.props.className)};
    return (<div className={cx(st.px24, st.center, st.left5px)}><Icon icon={svg} {...p} /></div>);
  }
}

LeftIcon.propTypes = {
  children: PropTypes.any,
  className: PropTypes.string,
};

export default LeftIcon;


