import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {constants} from 'utils/date';
import Months from './Months';
import Years from './Years';
import CalendarNextBack from './CalendarNextBack';
import TimeMouseClick from './TimeMouseClick';
import st from './Calendar.module.sass';
import tm from 'components/styles/Team.module.sass';

class CalendarTemplate extends Component {
  static showType = {
    calendar: 'CALENDAR',
    months: 'MONTHS',
    years: 'YEARS',
    time: 'TIME'
  };

  render() {
    const {
      year, month,
      showType, onChange = () => {},
      className: cnBox,
      children, lg, time = {},
      classBox
    } = this.props;

    switch (showType) {
      case CalendarTemplate.showType.months:
        return (
          <Months
            year={year}
            className={cx(tm.rootCalendar, classBox)}
            onChange={(y, m) => onChange({showType: CalendarTemplate.showType.calendar, year: y, month: m})}
            months={constants[lg].months}
          />
        );
      case CalendarTemplate.showType.years:
        return (
          <Years
            year={year}
            className={cx(tm.rootCalendar, classBox)}
            onChange={(y) => onChange({showType: CalendarTemplate.showType.calendar, year: y})}
          />
        );
      case CalendarTemplate.showType.calendar:
        return (
          <div className={cx(st.calendarBox, cnBox, tm.teamCalendarBox, tm.rootCalendar)}>
            <CalendarNextBack
              lg={lg}
              year={year}
              month={month}
              onClickYear={() => onChange({showType: CalendarTemplate.showType.years})}
              onClickMonth={() => onChange({showType: CalendarTemplate.showType.months})}
              onChange={(v) => onChange(v)}
            />
            {children}
          </div>
        );
      case CalendarTemplate.showType.time:
        const {hours, minutes} = time;
        return (
          <TimeMouseClick
            className={classBox}
            hours={hours}
            minutes={minutes}
            onChange={(t) => {
              if (onChange)
                if (t) onChange({showType: CalendarTemplate.showType.calendar, time: t});
                else onChange({showType: CalendarTemplate.showType.calendar});
            }}
          />
        );
      default:
        return null;
    }
  }
}

CalendarTemplate.defaultProps = {
  showType: CalendarTemplate.showType.calendar,
  lg: constants.lg.ru
};

CalendarTemplate.propTypes = {
  lg: PropTypes.string,
  classBox: PropTypes.string,
  className: PropTypes.string,
  children: PropTypes.any,
  showType: PropTypes.oneOf([
    CalendarTemplate.showType.years,
    CalendarTemplate.showType.months,
    CalendarTemplate.showType.calendar,
    CalendarTemplate.showType.time
  ]),
  year: PropTypes.number,
  month: PropTypes.number,
  time: PropTypes.object,
  onChange: PropTypes.func,
};

export default CalendarTemplate;


