import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {constants, datetimeToObject, datetimeFromObject, compareDateObjects, monthDown, monthUp, datetimeCurrentToObject} from 'utils/date';
import st from './Days.module.sass';
import tm from 'components/styles/Team.module.sass';

class Days extends Component {
  static type = {
    day: 'DAY',
    range: 'RANGE'
  };

  state = {
    from: {},
    to: {},
    lastChange: '',
  };

  _day = 0;
  _disable = false;
  handlerOnChange = (day, month, year, mouseDown) => {
    const {type, onChange} = this.props;
    let {from, to, lastChange} = this.state;

    const disable = this.isDisable(day, month, year, lastChange);
    if (disable || (this._disable && !mouseDown)) {
      if (!this._disable && !mouseDown && onChange) onChange({from, to, lastChange});

      this._disable = disable;
      return;
    }

    this._disable = disable;

    if (day !== this._day) {
      this._day = day;

      if (type === Days.type.day) {
        if (onChange) onChange({year, month, day});
      } else {
        if (lastChange === constants.rangeValueType.from && mouseDown) {
          from = {year, month, day};
          if (compareDateObjects(from, to) > 0) to = {};
        } else if (lastChange === constants.rangeValueType.to && mouseDown) {
          to = {year, month, day};
          if (compareDateObjects(to, from) < 0) from = {};
        } else if (to && to.day && lastChange !== constants.rangeValueType.from) {
          from = {year, month, day};
        } else if (from && from.day && lastChange !== constants.rangeValueType.to) {
          to = {year, month, day};
        }

        if (compareDateObjects(from, to) > 0) {
          const temp = to;
          to = from;
          from = temp;
          lastChange = lastChange === constants.rangeValueType.from ? constants.rangeValueType.to : constants.rangeValueType.from;
        }

        this.setState({from, to, lastChange});
      }
    }


    if (!mouseDown && onChange) onChange({from, to, lastChange});
  };

  componentWillMount() {
    const {from, to, lastChange} = this.props;
    this.setState({from, to, lastChange});
  }

  isDisable = (d, m, y, lastChange) => {
    const {
      type = Days.type.day,
      lteCurrentDate, gteCurrentDate,
      gteCurrentDateFrom, lteCurrentDateFrom,
      lteCurrentDateTo, gteCurrentDateTo
    } = this.props;

    let now = datetimeCurrentToObject();
    let disable = false;
    if (type === Days.type.day) {
      disable =
        ((lteCurrentDate && compareDateObjects(now, {year: y, month: m, day: d}) > 0)
        || (gteCurrentDate && compareDateObjects(now, {year: y, month: m, day: d}) < 0));
    } else {
      if (lastChange === constants.rangeValueType.from) {
        disable = (lteCurrentDateFrom && compareDateObjects(now, {year: y, month: m, day: d}) > 0) || (gteCurrentDateFrom && compareDateObjects(now, {year: y, month: m, day: d}) < 0);
      } else if (lastChange === constants.rangeValueType.to) {
        disable = (lteCurrentDateTo && compareDateObjects(now, {year: y, month: m, day: d}) > 0) || (gteCurrentDateTo && compareDateObjects(now, {year: y, month: m, day: d}) < 0);
      }
    }

    return disable;
  };

  render() {
    const {
      year, month, day,
      type = Days.type.day,
      className: cnBox, lg,
      lastChange
    } = this.props;
    let {from: {day: dayFrom, month: monthFrom, year: yearFrom} = {}, to: {day: dayTo, month: monthTo, year: yearTo} = {}} = this.state;


    const upMonth = datetimeToObject(datetimeFromObject({...monthUp({year, month}), day: 1}));
    const downMonth = datetimeToObject(datetimeFromObject({...monthDown({year, month}), day: 1}));


    const {dayInWeek, countDayInMonth} = datetimeToObject(datetimeFromObject({year, month, day: 1}));

    const renderDay = (d, m, y, position) => {

      const renderCurrentMonth = y === year && m === month;

      const f = !!d && compareDateObjects({year: yearFrom, month: monthFrom, day: dayFrom}, {year: y, month: m, day: d}) === 0;
      const t = !!d && compareDateObjects({year: yearTo, month: monthTo, day: dayTo}, {year: y, month: m, day: d}) === 0;

      return (
        <div
          key={`day-${y}-${m}-${d}`}
          className={cx(
            {[st.daysLeft]: position === 'left'},
            {[st.daysRight]: position === 'right'},
            st.daysContentItem,
            {
              [tm.teamCalendarItemBetween]:
              compareDateObjects({year: yearFrom, month: monthFrom, day: dayFrom}, {year: y, month: m, day: d}) < 0
              && compareDateObjects({year: yearTo, month: monthTo, day: dayTo}, {year: y, month: m, day: d}) > 0
            },
            {
              [st.daysDayRangeOnDay]:
              compareDateObjects({year: yearFrom, month: monthFrom, day: dayFrom}, {year: yearTo, month: monthTo, day: dayTo}) === 0
              && compareDateObjects({year: yearTo, month: monthTo, day: dayTo}, {year: y, month: m, day: d}) === 0
            },
            {[st.daysContentItemNotCurrentMount]: !renderCurrentMonth},
          )}
          onMouseDown={() => !!d && this.handlerOnChange(d, m, y, true)}
          onMouseUp={() => !!d && this.handlerOnChange(d, m, y, false)}
        >
          <div
            className={cx(
              {[st.daysContentItemSelected]: type === Days.type.day && !!day && day === d && year === y && month === m},
              st.cellContent,
              {[st.daysDayRangeFrom]: f},
              {[st.daysDayRangeTo]: t},
              {[st.calendarItemBetween]: (f || t) && dayFrom && dayTo},

              {[!this.isDisable(d, m, y, lastChange) ? tm.teamCalendarItem : st.daysContentItemDisable]: !!d},
            )}
          >
            <div className={st.cellContentText}>{d}</div>
          </div>
        </div>
      );
    };

    const buildWeek = (cd, dInWeek, dInMonth) => {
      let d = cd;
      let items = [];
      let end = false;
      let upMonthRenderDay = 1;
      for (let index = 0; index < 7; index++) {
        let position = index === 0 ? 'left' : index === 6 ? 'right' : '';
        //если собираем первый день месяца
        if (cd === 1) {
          //дни предидущего месяца
          if (dInWeek > index) {
            items.push(renderDay(downMonth.countDayInMonth + index - dInWeek + 1, downMonth.month, downMonth.year, position));
          }
          else {
            items.push(renderDay(d, month, year, position));
            d++;
          }
        } else {
          //дни следующего месяца
          if (d > dInMonth) {
            items.push(renderDay(upMonthRenderDay, upMonth.month, upMonth.year, position));
            upMonthRenderDay++;
            end = true;
          } else {
            items.push(renderDay(d, month, year, position));
            d++;
          }
        }
      }

      return {component: <div key={`week-${d}`} className={st.daysContentRow}>{items}</div>, day: d, end};
    };

    const renderMonth = (dInWeek, dInMonth) => {
      let items = [];
      let currentDay = 1;
      for (let w = 0; w < 6; w++) {
        const result = buildWeek(currentDay, dInWeek, dInMonth);
        items.push(result.component);
        currentDay = result.day;
        if (result.end) break;
      }

      return items;
    };

    return (
      <div className={cx(cnBox)}>
        <div className={st.daysBodyBox}>
          <div className={cx(st.daysColumnBox, tm.teamCalendarTitle)}>
            {constants[lg].daysOfWeek.map((a, i) => {
              return (
                <div
                  key={'days-of-week-' + i}
                  className={cx(
                    {[st.daysLeft]: i === 0},
                    {[st.daysRight]: i === 6},
                    st.daysColumnItem,
                    tm.teamCalendarTitleWeek,
                    {[tm.teamCalendarTitleWeekend]: i >= 5},
                  )}
                >
                  <div className={st.columnCellContentText}>
                    {a}
                  </div>
                </div>
              );
            })}
          </div>
          <div className={st.daysContentBox}>
            {renderMonth(dayInWeek, countDayInMonth)}
          </div>
        </div>
      </div>
    );
  }
}

Days.defaultProps = {
  lg: constants.lg.ru
};

Days.propTypes = {
  lg: PropTypes.string,
  onChange: PropTypes.func,
  year: PropTypes.number,
  month: PropTypes.number,

  day: PropTypes.number,

  gteCurrentDate: PropTypes.bool,
  lteCurrentDate: PropTypes.bool,

  from: PropTypes.object,
  to: PropTypes.object,

  gteCurrentDateFrom: PropTypes.bool,
  lteCurrentDateFrom: PropTypes.bool,
  lteCurrentDateTo: PropTypes.bool,
  gteCurrentDateTo: PropTypes.bool,

  type: PropTypes.oneOf([Days.type.day, Days.type.range]),
  className: PropTypes.string,
  lastChange: PropTypes.string
};

export default Days;


