import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import Calendar from 'components/common/Calendar';
import DateField from 'components/common/DateField';
import Days from 'components/common/Days';
import st from './OtcDate.module.sass';
import tm from 'components/styles/Team.module.sass';
import Align from 'components/common/Align'

class OtcDate extends Component {
  state = {
    open: false,
    value: '',
    isChange: false,
    refInput: undefined,
  };

  handlerInputOnChange = (v) => {
    const canBeChanged = !!this.props.defaultValue || !this.props.value;

    const {onChange} = this.props;
    this.setState({open: false, value: v, isChange: canBeChanged});
    if (onChange) onChange(v);
  };

  handlerOnRefInputChange = (ref) => {
    const {refInput} = this.state;
    if (refInput !== ref) {
      this.setState({refInput: ref});
    }
  };

  render() {
    const {
      placeholder,
      showTime,
      timeIsRequired,
      lteCurrentDate,
      gteCurrentDate,
      classBox,
    } = this.props;
    const {open, refInput} = this.state;

    const value = this.props.value || ((this.state.isChange && this.state.value) || this.props.defaultValue);

    return (
      <div className={cx(tm.rootCalendarBox, classBox)}>
        <DateField
          refInput={this.handlerOnRefInputChange}
          value={value}
          placeholder={placeholder}
          onChange={this.handlerInputOnChange}
          onFocus={() => this.setState({open: true})}
          onClickIcon={() => {this.setState({open: !open});}}
          onInput={() => {this.setState({open: false});}}
          ignoreTime={!showTime}
          gteCurrentDate={gteCurrentDate}
          lteCurrentDate={lteCurrentDate}
        />
        <Align
          onClickOutside={() => this.setState({open: false})}
          onScrollParent={(e) => {
            if (e && e.srcElement.className && e.srcElement.className.includes("otc-date-close-if-scroll-this"))
              this.setState({open: false});
          }}
          visible={open}
          refPositionRelative={refInput}
        >
          <Calendar
            onChange={this.handlerInputOnChange}
            className={cx(st.rootCalendar)}
            value={value}
            showTime={showTime}
            type={Days.type.day}
            timeIsRequired={timeIsRequired}
            gteCurrentDate={gteCurrentDate}
            lteCurrentDate={lteCurrentDate}
          />
        </Align>
      </div>
    );
  }
}

OtcDate.defaultProps = {
  showTime: false,
  timeIsRequired: false,
  gteCurrentDate: false,
  lteCurrentDate: false,
};

OtcDate.propTypes = {
  classBox: PropTypes.string,

  value: PropTypes.string,
  defaultValue: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,

  showTime: PropTypes.bool,
  timeIsRequired: PropTypes.bool,
  gteCurrentDate: PropTypes.bool,
  lteCurrentDate: PropTypes.bool,
};

OtcDate.descriptionProps = {
  placeholder: 'подсказка отображаемая в поле для ввода, если оно пустое',
  defaultValue: 'значение по умолчанию, отображается если пользователь не менял значение поля',
  onChange: 'функция вызываемая при изменении значения поля',
  showTime: 'отображать/скрыть время',
  timeIsRequired: 'время обязательно для заполенния',
  gteCurrentDate: 'не больше текущей даты',
  lteCurrentDate: 'не меньше текущей даты'
};


export default OtcDate;


