import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {getPropTypeName} from './helper';
import {concatTitle} from 'utils/str';
import st from './UiKit.module.sass';

class Box extends Component {
  render() {
    const {label, children, params, name} = this.props;

    const renderParams = (prms) => {
      if (!prms) return null;

      const {propTypes = {}, defaultProps = {}, descriptionProps = {}} = prms;

      const propNames = Object.keys(propTypes);

      let lines = [];
      for (let i = 0; i < propNames.length; i++) {
        const propName = propNames[i];
        const type = getPropTypeName(propTypes[propName]);

        const typeText = concatTitle('тип: ', type, '');
        const defText = concatTitle('по умолчанию: \'', defaultProps[propName], '\'');
        const descText = concatTitle('', descriptionProps[propName]);

        lines.push((
          <div className={st.boxParams}>
            <div className={st.flex5}>
              <div className={st.boxParamsPropName}>{propName}</div>
              <div className={st.boxParamsPropType}>{typeText}</div>
              <div className={st.boxParamsPropDefValue}>{defText}</div>
            </div>
            <div className={st.boxParamsPropDescription}>{descText}</div>
          </div>
        ));
      }

      return lines.map(a => {return (<div>{a}</div>)});
    };

    return (
      <div className={st.uikitBox}>
        <div className={st.uikitTitle}>{name}:</div>
        <div className={st.uikitLabel}>
          {label}
        </div>
        <div className={st.uikitTitle}>Параметры:</div>
        <div className={st.uikitParams}>
          {renderParams(params)}
        </div>
        <div className={st.uikitTitle}>Демо:</div>
        <div className={st.uikitDemo}>
          {children}
        </div>
      </div>
    );
  }
}

Box.propTypes = {
  label: PropTypes.string,
  params: PropTypes.string,
  children: PropTypes.element,
  name: PropTypes.string
};

export default Box;


