import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import {to00} from 'utils/str';
import BackIcon from '../icons/BackIcon';
import stNextBack from './NextBack.module.sass';
import st from './TimeMouseClick.module.sass';
import tm from 'components/styles/Team.module.sass';

class TimeMouseClick extends Component {
  static showType = {
    hh: 'HH',
    mm: 'MM'
  };

  state = {
    showType: TimeMouseClick.showType.hh,
    hours: undefined,
    minutes: undefined,
  };

  componentWillMount() {
    const {hours, minutes} = this.props;
    this.setState({hours, minutes});
  }

  render() {
    const {className: cnBox, onChange} = this.props;
    const {showType, hours, minutes} = this.state;

    const renderHH = () => {
      let rowsHH = [];
      let rowHH = [];
      for (let i = 0; i < 24; i++) {
        rowHH.push(
          <div
            className={cx(st.cell, {[st.cellSelected]: i === hours})}
            onClick={() => {
              this.setState({showType: TimeMouseClick.showType.mm, hours: i});
            }}
          >
            <div>{to00(i)}</div><div className={st.label400}>:00</div>
          </div>
        );
        if (rowHH.length >= 4) {
          const rw = rowHH;
          rowsHH.push(<div className={st.row}>{rw}</div>);
          rowHH = [];
        }
      }

      return <div className={st.table}>{rowsHH}</div>;
    };

    const renderMM = () => {
      const hh = to00(hours);
      let rowsMM = [];
      let rowMM = [];
      for (let i = 0; i < 60; i += 5) {
        rowMM.push((
          <div
            className={cx(
              st.cell,
              {[st.cellSelected]: i === minutes}
            )}
            onClick={() => {
              this.setState({minutes: i});
              if (onChange) onChange({hours, minutes: i});
            }}
          >
            <div className={st.label400}>{`${hh}:`}</div><div>{`${to00(i)}`}</div>
          </div>
        ));

        if (rowMM.length >= 4) {
          const rw = rowMM;
          rowsMM.push((<div className={st.row}>{rw}</div>));
          rowMM = [];
        }
      }

      return (<div className={st.table}>{rowsMM}</div>);
    };

    const renderContent = () => {
      switch (showType) {
        case TimeMouseClick.showType.hh:
          return renderHH();
        case TimeMouseClick.showType.mm:
          return renderMM();
        default:
          return null;
      }
    };

    return (
      <div className={cx(tm.rootCalendar, st.tmcBox, cnBox)}>
        <div className={cx(st.tmcTitle, stNextBack.nextbackHeaderStyle)}>
          <BackIcon
            className={st.tmcTitleBack}
            onClick={() => {
              if (showType === TimeMouseClick.showType.mm)
                this.setState({showType: TimeMouseClick.showType.hh});
              else if (onChange)
                onChange();
            }}
          />
        </div>
        <div className={st.tableBox}>
          {renderContent()}
        </div>
      </div>
    );
  }
}

TimeMouseClick.propTypes = {
  className: PropTypes.string,
  onChange: PropTypes.func,
  hours: PropTypes.number,
  minutes: PropTypes.number,
};

export default TimeMouseClick;


