import React, {Component} from 'react';
import PropTypes from 'prop-types';
import st from './Align.module.sass';
import {eventAdd, eventRemove} from 'utils/func';
import uid from 'uid';

class Align extends Component {
  state = {
    top: 0,
    left: 0,
    bottom: 0,
    right: 0
  };

  correctPosition = (ref) => {
    let {refPositionRelative = ref} = this.props;

    if (refPositionRelative) {
      const rect = refPositionRelative.getBoundingClientRect();
      this.setState({bottom: rect.bottom, left: rect.left, top: rect.top, right: rect.right});
    }
  };

  doThisAndAllParent = (ref, func) => {
    if (!ref) return;
    func(ref);

    if (ref.parentNode) {
      this.doThisAndAllParent(ref.parentNode, func);
    }
  };

  handlerOnScroll = (e) => {
    this.correctPosition();

    const {onScrollParent} = this.props;
    if (e && e.srcElement) {
      const element = e.srcElement;
      if (element.nodeName !== "DIV") return;
    }

    if (onScrollParent) onScrollParent(e);
  };

  componentWillUnmount() {
    let {refPositionRelative} = this.props;
    this.unsubscribeFromEvent(refPositionRelative);
  }

  handlerOnClickOutside = (e) => {
    const {onClickOutside} = this.props;
    if (!onClickOutside) return;

    const {top, right, bottom, left} = this.state;
    const {x, y} = e;

    if (top && right && bottom && left) {
      if ((top > y || bottom < y) || (x < left || x > right)) {
        onClickOutside();
      }
    }
  };

  componentWillReceiveProps(nextProp) {
    let {refPositionRelative, visible} = nextProp;

    //если нужно показать и был скрыт
    if (refPositionRelative && visible && !this.props.visible) {
      this.subscribeToEvents(refPositionRelative);
      this.correctPosition(refPositionRelative);
    }
    //если нужно скрыть и был показан
    else if (!visible && this.props.visible && this.props.refPositionRelative) {
      this.unsubscribeFromEvent(this.props.refPositionRelative);
    }
    //если поменялся ref а старый существует и был показан
    else if (refPositionRelative && this.props.refPositionRelative && refPositionRelative !== this.props.refPositionRelative && this.props.visible) {
      this.unsubscribeFromEvent(this.props.refPositionRelative);
      this.subscribeToEvents(refPositionRelative);
      this.correctPosition(refPositionRelative);
    }
    //если ref пропал
    else if (!refPositionRelative && this.props.refPositionRelative) {
      this.unsubscribeFromEvent(this.props.refPositionRelative);
    }
    //если ref появился и нужно показать
    else if (refPositionRelative && !this.props.refPositionRelative && visible) {
      this.subscribeToEvents(refPositionRelative);
      this.correctPosition(refPositionRelative);
    }
  }

  subscribeKey = uid();

  subscribeToEvents = (ref) => {
    this.doThisAndAllParent(ref, (rf) => rf.onscroll = eventAdd(rf.onscroll, this.handlerOnScroll, this.subscribeKey));
    window.onresize = eventAdd(window.onresize, this.correctPosition, this.subscribeKey);
    window.onclick = eventAdd(window.onclick, this.handlerOnClickOutside, this.subscribeKey);
  };

  unsubscribeFromEvent = (ref) => {
    this.doThisAndAllParent(ref, (rf) => rf.onscroll = eventRemove(rf.onscroll, this.subscribeKey));
    window.onresize = eventRemove(window.onresize, this.subscribeKey);
    window.onclick = eventRemove(window.onclick, this.subscribeKey);
  };

  render() {
    const {bottom, left} = this.state;
    let {refPositionRelative, visible, children, zIndex} = this.props;
    if (!visible || !refPositionRelative) return null;
    return (
      <div
        onClick={(e) => e.stopPropagation()}
        className={st.popup}
        style={{top: bottom, left, zIndex}}
      >
        {children}
      </div>
    );
  }
}

Align.defaultProps = {
  zIndex: 1
};

Align.propTypes = {
  className: PropTypes.string,
  zIndex: PropTypes.number,

  refPositionRelative: PropTypes.any,
  children: PropTypes.element,

  visible: PropTypes.bool,
  onScrollParent: PropTypes.func,
  onClickOutside: PropTypes.func,
};

export default Align;


