import PropTypes from 'prop-types';

export function getPropTypeName (type) {
  switch (type) {
    case PropTypes.string:
      return 'string';
    case PropTypes.number:
      return 'number';
    case PropTypes.func:
      return 'func';
    case PropTypes.bool:
      return 'bool';
  }
}


