export function concatEvents(evnts) {
  if (!evnts || evnts.length <= 0) return () => {};
  const eventsNotNull = evnts.filter(a => !!a);
  return (args) => {
    for (let i = 0; i < eventsNotNull.length; i ++) {
      const event = eventsNotNull[i];
      if (event) event(args);
    }
  };
}


function eventEmitter() {
  const events = [];

  let eventHandler = (args) => {
    for (let i = 0; i < events.length; i ++) {
      const event = events[i];
      if (event) event(args);
    }
  };

  eventHandler.typeName = 'eventHandler';

  eventHandler.events = events;

  eventHandler.on = (evn, owner) => {
    evn.owner = owner;

    let any = false;
    for (let i = 0; i < events.length; i++) {
      let event = events[i];
      if (event.owner === owner) {
        any = true;
        events[i] = evn;
      }
    }

    if (!any) {
      events.push(evn);
    }
  };

  eventHandler.remove = (owner) => {
    for (let i = events.length - 1; i >= 0; i--) {
      let event = events[i];
      if (event.owner === owner) {
        events.splice(i, 1);
      }
    }
  };

  return eventHandler;
}

export function eventAdd(evn, event, owner) {
  if (!evn) evn = eventEmitter();
  if (evn && evn.typeName !== 'eventHandler') {
    const emitter = eventEmitter();
    emitter.on(evn, 'none');
    evn = emitter;
  }

  evn.on(event, owner);
  return evn;
}

export function eventRemove(evn, owner) {
  if (!evn) return;
  if (evn && evn.typeName !== 'eventHandler') return;

  evn.remove(owner);
  if (evn.events.length <= 0) return null;

  return evn;
}


export function isnull(value, defaultValue) {
  if (value === 0 || value === false || value === '') return value;

  return value || defaultValue;
}

export function exists(value) {
  return value === 0 || value === false || value === '' || value;
}


