import React, {Component} from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import st from './TextField.module.sass';
import cl from 'components/styles/TeamColor.module.sass';

class TextField extends Component {
  state = {
    isChange: false
  };

  handlerOnChange = (e) => {
    const {onChange} = this.props;
    let value = e.target.value;

    this.setState({isChange: true});
    if (onChange) onChange(value);
  };

  render() {
    const {
      value, defaultValue,
      placeholder, icon,
      className: cnBox,
      onFocus, onBlur,
      classInput: cnInput,
      refInput} = this.props;
    const {isChange} = this.state;

    return (
      <div className={cx(st.textfieldBox, cnBox)}>
        <input
          ref={refInput}
          className={cx(st.textfieldInput, cl.componentBorder, cnInput)}
          type='text'
          value={value || (!isChange && defaultValue) || ''}
          placeholder={placeholder}
          onInput={this.handlerOnChange}
          onBlur={onBlur}
          onFocus={onFocus}
        />
        {icon &&
        <div className={st.textfieldIconBox}>
          {icon}
        </div>
        }
      </div>
    );
  }
}

TextField.propTypes = {
  refInput: PropTypes.func,
  className: PropTypes.string,
  classInput: PropTypes.string,
  value: PropTypes.string,
  defaultValue: PropTypes.string,
  placeholder: PropTypes.string,
  icon: PropTypes.element,
  onChange: PropTypes.func,
  onBlur: PropTypes.func,
  onFocus: PropTypes.func,
};

export default TextField;


